 &__slider {
  background-color: $primaryColor;
  padding: 64px 35px;
  max-width: 958px;
  width: 100%;
  margin: 0 auto;
 }
 &__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  // grid-template-rows: repeat(2, 127px);
  gap: 13px;
 }
 &__grid-images {
  display: grid;
  grid-template-columns: 267px 127px;
  grid-template-rows: 127px 127px;
  gap: 13px;
 }
 &__image {
  object-fit: cover;
  border-radius: 5px;
 }
 &__image-box {
  grid-area: 1/1/3/2;
 }
 &__images-box {
  display: grid;
  grid-area: 1/2/3/3;
  gap: 13px;
 }
 &__image--big {
  width: 267px;
  height: 267px;  
 }
 &__image--small {
  width: 127px;
  height: 127px;
 }
 &__content {
  max-width: 385px;
  width: 100%;
 }
 &__content-title {
  @include text (36px, 400, 1.2, $accentColor);
  font-family: "Quicksand";
  margin-bottom: 32px;
 }
 &__content-text {
  @include text (14px, 400, 1.8, #DFE2EF);
  font-family: "Roboto";
  max-width: 408px;
  width: 100%;
  margin-bottom: 30px;
 }
 &__link {
  @include text (13px, 700, 1.2, $buttonColor);
  font-family: "Quicksand";
  padding: 13px 15px;
 }
 &__link--blue {
  background-color: $buttonColorBg;
  margin-right: 8px;
 }
 &__link--transparent {
  color: #A6B6DA;
 } 


}